import React from 'react'
import dado1 from "../img/productos/dados/1.-Dado Apex M1P08.jpg"
import dado2 from "../img/productos/dados/2.-Dado Apex SF-13MM13.jpg"
import dado3 from "../img/productos/dados/3.-Dado corto Apex 13MM13.jpg"
import dado4 from "../img/productos/dados/4.-Dado hexagonal Apex 10MM23.jpg"
import dado5 from "../img/productos/dados/5.-Dado magnetico Apex MX-1108.jpg"
import dado6 from "../img/productos/dados/6.-Dado torx Apex TX-1408.jpg"
import './dados.css'
import {Link} from "react-router-dom";

function Dados() {
  return (
    <div className='dados'>
    <div><Link to="/dado1"><img src={dado1}></img></Link></div>
    <div><Link to="/dado2"><img src={dado2}></img></Link></div>
    <div><Link to="/dado3"><img src={dado3}></img></Link></div>
    <div><Link to="/dado4"><img src={dado4}></img></Link></div>
    <div><Link to="/dado5"><img src={dado5}></img></Link></div>
    <div><Link to="/dado6"><img src={dado6}></img></Link></div>
    </div>
  )
}

export default Dados